import React from "react";
import "./DropDownCatalog.scss";
import { useNavigate } from "react-router-dom";
export const DropDownCatalog: React.FC = () => {
  const navigate = useNavigate();

  const handlePurposeClick = (purpose: string) => {
    //     
    navigate(`/catalog?purpose=${encodeURIComponent(purpose)}`);
  };
  return (
    <div className="drop-down">
      <div className="purpose">
        <ul className="purpose__list">
          {[
            " ",
            "   ",
            " ",
            " (, , )",
            "  ",
            "",
            "  ",
            "",
          ].map((item) => (
            <li
              key={item}
              className="purpose__link"
              onClick={() => handlePurposeClick(item)} //  
            >
              {item}
            </li>
          ))}
          <li
            className="purpose__link"
            onClick={() => handlePurposeClick("all")} //    
          >
            <span> </span>
          </li>
        </ul>
      </div>

      <div className="vitamins">
        <div className="vitamins__category">
          <h3 className="vitamins__category--title"> D</h3>
          <ul className="vitamins__category--list">
            <li className="vitamins__category--item"> D 400 -5000ME</li>
            <li className="vitamins__category--item"> D 5000-25000ME</li>
          </ul>
        </div>

        <div className="vitamins__category">
          <h3 className="vitamins__category--title"> K</h3>
        </div>

        <div className="vitamins__category">
          <h3 className="vitamins__category--title"> B</h3>
          <ul className="vitamins__category--list">
            <li className="vitamins__category--item"></li>
            <li className="vitamins__category--item"> </li>
          </ul>
        </div>

        <div className="vitamins__category">
          <h3 className="vitamins__category--title"> D</h3>
        </div>

        <div className="vitamins__category">
          <h3 className="vitamins__category--title"> A</h3>
        </div>

        <div className="vitamins__category">
          <h3 className="vitamins__category--title"> E</h3>
        </div>

        <div className="vitamins__category">
          <h3 className="vitamins__category--title"> C</h3>
        </div>
      </div>
    </div>
  );
};
